main {
  width: 82vw;
  position: relative;
  float: right;
  height: 92vh;
  z-index: 100;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
    url(../../shared//BG/background.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.logboek__container {
  display: flex;
  padding-left: 135px;
  padding-top: 80px;
}

.logboek__new__workout {
  margin-right: 100px;
  background-color: white;
  width: 675px;
  height: 333px;
  border-radius: 10px;
  padding-top: 20px;
  padding-left: 15px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.8);
}

.logboek__new__workout h2 {
  font-weight: 400;
}

.logboek__new__workout form {
  margin-top: 20px;
  display: flex;
  width: 100%;
  flex-direction: column;
}

.logboek__new__workout form input:nth-child(1) {
  width: 30%;
  padding: 3px 10px;
  margin-bottom: 18px;
  outline: none;
}

.logboek__new__workout form textarea {
  padding: 3px 10px;
  width: 50%;
  height: 125px;
  overflow-y: scroll;
  outline: none;
}

.logboek__new__workout form textarea:focus,
input:focus {
  border: 0.3px solid #4528d5;
}

.logboek__new__workout form textarea::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: transparent;
}

.logboek__new__workout form textarea::-webkit-scrollbar {
  width: 12px;
}

.logboek__new__workout form textarea::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #bbbbbb;
}

.logboek__new__workout form button {
  margin-top: 32px;
  width: 120px;
  height: 32px;
  color: white;
  background-color: #4528d5;
  border: none;
  cursor: pointer;
  transition: all 200ms;
}

.logboek__new__workout form button:hover {
  color: #4528d5;
  background-color: white;
  border: 1px solid #4528d5;
}

/* Recent  Sectie */

.logboek__recent__workouts {
  background-color: white;
  height: 333px;
  width: 405px;
  border-radius: 10px;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 80px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.8);
}

.logboek__recent__workouts h2 {
  font-weight: 400;
  margin-bottom: 15px;
}

.recent__workouts__header {
  display: flex;
  justify-content: space-between;
}

.recent__workouts__body ul li a {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
  color: black;
}
